.wordCarousel {
    font-size: 30px;
    font-weight: 100;
    color: #c9d1d3;
    display: flex;
}

.wordCarousel-div {
    overflow: hidden;
    position: relative;
    height: 65px;
    padding-top: 10px;
    margin-top: -10px;
}

.wordCarousel-li {
    font-family: "Roboto Condensed";
    color: #c9d1d3;
    font-weight: 700;
    padding: 0 10px;
    height: 45px;
    margin-bottom: 45px;
    display: block;
}

.flip3 { -webkit-animation: flip3 8s cubic-bezier(0.23, 1, 0.32, 1.2) infinite; animation: flip3 8s cubic-bezier(0.23, 1, 0.32, 1.2) infinite; }

@-webkit-keyframes flip3 {
    0% { margin-top: -270px; }
    5% { margin-top: -180px; }
    33% { margin-top: -180px; }
    38% { margin-top: -90px; }
    66% { margin-top: -90px; }
    71% { margin-top: 0px; }
    99.99% { margin-top: 0px; }
    100% { margin-top: -270px; }
}

@keyframes flip3 {
    0% { margin-top: -270px; }
    5% { margin-top: -180px; }
    33% { margin-top: -180px; }
    38% { margin-top: -90px; }
    66% { margin-top: -90px; }
    71% { margin-top: 0px; }
    99.99% { margin-top: 0px; }
    100% { margin-top: -270px; }
}
